.avatar{
    border: 2px solid darkorange;
    height: 50px;
    width: 50px;
    border-radius: 50%;
}

.like{
    cursor: pointer;
    margin-right: 30px;
    margin-left: 30px;
    display: block;
    text-align: left;
    margin-top: 15px;
    font-size: 20px;
    color: blue;
}

.postContent{
    display: inline-block;
    margin-left: 30px;
    font-size: 25px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.post{
    margin: 20px 0;
    background-color: skyblue;
    border: 2px solid lightslategrey;
    border-radius: 30px;
    padding: 10px 30px;
}

.likeNumber{
    color: white;
}