.app-wrapper{
  display: grid;
  grid-template-areas: "h h" "n a" "f f";
  grid-template-rows:60px 1fr 120px;
  grid-template-columns: 2fr 10fr;
  grid-row-gap: 1px;
  grid-column-gap: 1px;
  height: 1500px;
  width: 1300px;
  margin-right: auto;
  margin-left: auto;
  text-decoration: none;
}

.app-wrapper-article{
  grid-area: a;
  background-color: coral;
}

body{
  background-color: lightgoldenrodyellow;
}