.dialog{
    font-size: 25px;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    margin: 15px 0;
    text-decoration: none;
}

.dialog a{
    text-decoration: none;
}

.dialog a .active {
    color: rgb(21, 136, 78);
}

.dialogImg{
    height: 40px;
    border-radius: 50%;
}

.dialogPerson{
    position: relative;
    bottom: 10px;
    left: 10px;
}