#statusContainer{
    height: 100px;
}

#status{
    position: relative;
    left: 250px;
    bottom: 100px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 50px;
    padding: 10px;
    color: darkcyan;
    width: 700px;
    background-color: hsl(203, 11%, 77%);
    text-align: center;
    border-radius: 30px;
}

#statusinput{    
    position: fixed;
    top: 300px;
    right: 550px;
    border: 2px solid gray;
    padding: 40px;
    height: 90px;
    width: 300px;
    background-color: #4481c7;
    border-radius: 30px;
    z-index: 10;

}

#statusBar{
    height: 30px;
    width: 300px;
    border: 1px solid #4481c7 ;
    border-radius: 15px;
    padding-left: 10px;
}

.btn{

    height: 40px;
    width: 90px;
    border-radius: 15px;
}

#update{
    position: relative;
    left: 50px;
    top: 20px;
}

#cancel{
    position: relative;
    left: 80px;
    top: 20px;
}





