.Header{
  position: fixed;
  grid-area: h;
  background-color: cornflowerblue;
  height: 60px;
  width: 1300px;
  z-index: 20;
}
  
.Header img{
  width: 100px;
}

.login{
  position: relative;
  top: 15px;
  right: 60px;
  float: right;
}

.login a{
  text-decoration: none;
  font-size: 25px;
  color: blue;
  margin-left: 20px;
  font-family: Georgia, 'Times New Roman', Times, serif;
}

.login a:active{
  color: red;
}

#auth{
  position: relative;
  bottom: 5px;
}

#auth img{
  width: 40px;
  height: 40px;
  border-radius: 10px;
}

#username{
  position: relative;
  bottom: 15px;
  right: 10px;
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-size: 20px;
  color: darkblue;
}

#signout{
  position: relative;
  bottom: 15px;
  left: 10px;
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-size: 20px;
  color: darkblue;
}